h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
    // color: #000
    line-height: 1.4;
    font-weight: 600; }

h1, .h1 {
    @include respond-to(tablet) {
        font-size: 2rem; }
    @include respond-to(phone) {
        font-size: 1.8rem; } }

h2, .h2 {
    font-size: 1.6rem;
    @include respond-to(laptop) {
        font-size: 1.6rem; }
    @include respond-to(phone) {
        font-size: 1.5rem; } }

h4, .h4 {
    @include respond-to(tablet) {
        font-size: 1.4rem; }
    @include respond-to(phone-land) {
        font-size: 1.2rem; } }

h5, .h5 {
    @include respond-to(tablet) {
        font-size: 1.1rem; }
    @include respond-to(phone-land) {
        font-size: 1rem; } }
