@import '../sass/_vars';

.basket-panel {
    padding: 30px 20px;
    @include respond-to(phone-land) {
        padding: 25px 0px; }
    button {
        width: 100%; }
    &__view-more {
        font-weight: 600; } }

.basket {
    &__font-size {
        font-size: 16px; }
    &__input {
       input {
           border: none!important;
           height: 40px;
           border-radius: 0!important;
           outline: none;
           font-size: 14px;
           padding: .375rem .75rem !important;
           width: 100% !important; } } }
.basket-list {
    &__form {
        background-color: $sky;
        padding: 35px;
        @include respond-to(phone-land) {
            padding: 35px 15px;
            margin: 0 -15px; }
        &__border {
            border: 1px solid $gray;
            padding: 30px;
            &__red {
                border: 1px solid $primary;
                padding: 30px; } } }

    &__border-left {
        @include respond-to(phone-land) {
            border: none;
            padding: none; }
        border-left: 1px solid #C1C6CC;
        padding-left: 37px;
        margin-left: -37px; }
    &__header {
        background: $light-gray; }
    &__quad {
        @include respond-to(phone-land) {
            display: none; }
        width: 15px;
        height: 15px;
        background: #C1C6CC;
        margin-top: -57px;
        margin-left: -45px;
        position: absolute;
        @include respond-to(laptop) {
            margin-left: -46px; }
        @include respond-to(tablet) {
            margin-left: -46px; }
        @include respond-to(phone-land) {
            margin-left: -5px; } } }
