.product-card-simple,
.product-card-horizontal {
    margin-bottom: 30px;
    @include respond-to(phone) {
        height: 180px;
        margin-bottom: 17px; }

    box-shadow: $shadow;
    height: 300px;
    color: $black;
    &:hover .info__button {
        color: $primary;
        svg {
            fill: $primary; } }


    .info {
        &__title {
            word-break: break-word;
            height: 40px;
            overflow: hidden; }

        &__button {
            svg {
                transition: $transition;
                fill: $white;
                &:hover {
                    fill: $primary; } }
            background: none;
            border: none;
            color: $gray;
            transition: $transition; } } }

.product-card-simple {
    &__photo {
        height: 200px;

        img {
            max-width: 100%;
            max-height: 100%;
            object-fit: cover; }

        @include respond-to(phone) {
            height: 130px; } }

    @include respond-to(phone) {
        flex-direction: row-reverse; } }

.product-card-horizontal {
    .info {
        flex: 1;
        &__button {
            right: 37px; } }

    &__photo {
        flex: 2;
        img {
            max-width: 100%;
            max-height: 100%;
            object-fit: contain; } } }

