.action-card {
    background: $white;
    padding: 20px;
    cursor: pointer;
    margin-bottom: 30px;
    height: 393px;
    min-width: 200px;

    .visible-content {
        box-shadow: $shadow; }

    .content {
        height: 85px; }

    .header {
        margin-bottom: 5px;
        z-index: 1;

        &__badge {
            color: $white;
            width: 90px;
            height: 20px;
            transform: translateX(-20px); }

        &__actions {
            font-size: 17px; } }

    &__title a {
        color: $black; }

    &__info {
        @include respond-to(tablet) {
            font-size: 12px; } }

    &__img-wrapper {
        width: 100%;
        height: 173px;
        margin-bottom: 10px; }

    &__img {
        max-width: 100%;
        max-height: 100%;
        margin: 0 auto;
        align-self: center;
        object-fit: cover;
        @include respond-to(phone) {
            margin-bottom: 5px; } }


    .hover-content {
        opacity: 0;
        left: 0;
        right: 0;
        height: 100px;
        background: $white;
        padding: 0 20px;
        transform: translateY(-100px);
        z-index: 22; }

    &:hover .hover-content {
        animation: hover-content $transition cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
        opacity: 1; }

    @keyframes hover-content {
        0% {
            transform: translateY(-20px); }

        100% {
            transform: translateY(0px); } }

    .horizontal-content {
        &__text {
            flex: 1; }

        &__img {
            flex: 2; } }

    .footer {
        .add-to-cart {
            background: none;
            border: none;
            height: 30px;
            &:hover {
                color: $primary; } }

        &__cart-button,
        &__headphone-button {
            background: $black;
            padding: 8px;
            &:hover {
                background: $primary; } }

        &__headphone-button {
            background: $dark-gray; } }

    .prices {
        min-width: 75px;
        font-weight: bold;
        &__old-price {
            text-decoration: line-through;
            height: 20px; }

        &__new-price {
            font-size: 24px; } } }

.view-more:hover {
    color: $primary; }
