.payment-form {
    width: 100%;
    padding: 40px;
    background: $sky;

    @include respond-to(phone) {
        padding: 35px 15px; }

    &__choose-method {
        width: 90%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 40px;

        @include respond-to(phone-land) {
            width: 100%; } }

    &__promocode {
        width: 90%;
        display: flex;
        justify-content: space-between;

        @include respond-to(phone-land) {
            flex-wrap: wrap;
            width: 100%; } }

    input[type="text"] {
        padding: 13px 10px;
        width: 60%;
        margin-right: 20px;
        border: none;

        @include respond-to(phone-land) {
            width: 100%;
            margin-right: 0;
            margin-bottom: 15px; } }

    label {
        font-weight: 600; }

    fieldset {
        @include respond-to(phone-land) {
            margin-bottom: 20px; } }

    button {
        font-weight: 500; }

    .stillage-button {
        @include respond-to(phone-land) {
            width: 100%; } } }

