.receive-wrapper {
    height: 250px;
    background: $sky;
    transition: height $transition;

    @include respond-to(phone) {
        height: 120px; }


    &__optional_active {
        height: 500px;

        @include respond-to(phone) {
            height: 610px; } }

    &__optional_active .optional-receive {
        visibility: visible; } }

.recieve-method {
    @extend .buyer-form-wrapper;

    display: flex;
    height: 245px;

    @include respond-to(phone) {
        justify-content: space-between;
        height: 120px; }


    &__title {
        @include respond-to(phone) {
            &:hover {
                color: $primary; } } }

    &__card {
        padding: 25px 20px;
        border: 1px solid $gray;
        height: 100%;
        width: 280px;
        margin-right: 25px;
        cursor: pointer;
        transition: border-color $transition;

        @include respond-to(phone) {
            padding: 0;
            width: auto;
            border: none; }


        @include respond-to(phone) {
            margin-right: 0; }


        &_active {
            border-color: $primary;

            @include respond-to(phone) {
                .recieve-method__title {
                    color: $primary;

                    &:hover {
                        color: $primary; } } } } } }

.optional-receive {
    visibility: hidden;
    display: flex;
    padding: 0 40px 40px 40px;
    width: 100%;

    @include respond-to(phone-land) {
        padding: 0 15px 35px 15px; }


    @include respond-to(phone) {
        flex-direction: column; }


    &__sections {
        @extend .recieve-method__card;

        padding: 0;
        border: 0; }

    &__element {
        @extend .buyer-form__element;

        height: 50px;
        width: 100%;

        @include respond-to(phone-land) {
            margin-bottom: 20px; } }

    &__price {
        position: absolute;
        font-size: 24px;
        font-weight: 700;
        color: $dark-gray;
        right: 40px;
        bottom: 70px;

        @include respond-to(phone-land) {
            bottom: 55px; } }

    &__zone:hover .popup-info {
        visibility: visible;
        transition: all $transition; }

    &__zone.select-arrow-active .popup-info {
        left: -50px;
        visibility: hidden; }


    &__zone {
        .select-items {
            max-height: 250px;
            background: lighten($black, 8%); }

        .select-items div, .same-as-selected {
            border: none;
            color: $white;
            text-align: center;
            padding: 4px 16px; }

        .select-items div:hover {
            background: lighten($black, 6%);
            border: none;
            color: $primary; }

        .select-items div:nth-child(2) {
            padding-top: 13px; }

        .select-items div:last-child {
            padding-bottom: 13px; }

        div.same-as-selected {
            background: none;
            color: $primary;
            font-weight: 700; }

        .select-items > div:first-child {
            background: url(../../img/checkout/zone-map.png) no-repeat center;
            background-size: cover;
            height: 125px;
            border: none;
            pointer-events: none; }

        .select-items div:nth-child(n+2)::before {
            content: '';
            height: 10px;
            width: 10px;
            position: absolute;
            left: 100px;
            margin-top: 5px; }

        .select-items div:nth-child(2)::before {
            background-color: $primary; }

        .select-items div:nth-child(3)::before {
            background-color: $gray; }

        .select-items div:last-child::before {
            background-color: darken($black, 5%); } } }

.popup-info {
    visibility: hidden;
    position: absolute;
    left: 0;
    top: 55px;
    max-width: 250px;
    width: 200px;
    padding: 10px;
    font-size: 11px;
    background-color: rgba(38, 50, 63, 0.8);
    transition: visibility $transition;

    .sign {
        position: absolute;
        left: -8px;
        top: -13px; } }

.sign {
    height: 15px;
    width: 15px;
    margin: 9px 0;
    font-size: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: $dark-gray;
    border-radius: 50%;
    color: $white;

    @include respond-to(tablet) {
        margin: 6px 0; }


    @include respond-to(phone-land) {
        margin: 4px 0; } }
