.overlay {
    position: fixed;
    background: $gray;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0;
    visibility: hidden;
    transition: all $transition;
    z-index: 33;

    &_active {
        opacity: .8;
        visibility: visible;
        cursor: pointer; } }

.modal {
    overflow: hidden;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    transform: scale(0);
    transition: transform $transition;
    z-index: 64;

    &_active {
        display: block;
        transform: scale(1); } }

.price-form {
    position: relative;
    padding: 60px 40px;
    background: $white;
    transition: $transition;
    width: 400px;
    height: auto;
    top: 20%;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;

    @include respond-to(phone) {
        top: 0;
        width: 100%;
        height: 100%;
        padding-top: 50%; }

    &__input {
        border: none;
        border-bottom: 1px solid $gray;
        width: 100%;
        transition: $transition; }

    &__input:focus {
        border-color: $black; }

    &__input_poluted {
        border-bottom: 1px solid $black; }

    &__close-btn {
        color: $white;
        cursor: pointer;
        right: -40px;
        top: 0;
        position: absolute;
        &:hover svg path {
            fill: $primary; }

        @include respond-to(phone) {
            right: 30px;
            top: 100px;
            svg path {
                fill: $gray; } } } }
