.information-button {
    box-shadow: $shadow;
    height: 64px;
    padding: 20px;
    cursor: pointer;
    // max-width: 288px
    font-weight: 600;
    @include respond-to(phone-land) {
        max-width: 100%; }

    span {
        padding-right: 20px; }

    .open {
        transition: all $transition ease;
        visibility: hidden;
        position: absolute;
        right: 32px;
        opacity: 0; }

    .close {
        transition: all $transition ease;
        visibility: hidden;
        position: absolute;
        right: 32px;
        opacity: 0; } }

.information-button-active {
    @extend .information-button;
    background: $gray;
    color: $white; }

.open.active {
    visibility: visible;
    opacity: 1; }

.close.active {
    visibility: visible;
    opacity: 1; }
