.mobile-menu-overlay {
    background: rgba(38, 50, 63, 0.781);
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1040;
    opacity: 0;
    display: none;
    transition: opacity $transition;

    &_active {
        display: block;
        opacity: 1; } }


.mobile-menu-modal {
    overflow: hidden;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    transform: translateX(-1000px);
    transition: transform $transition;
    display: none;
    @include respond-to(phone-land) {
        display: block; }

    &_active {
        transform: translateX(0); } }

.drawer-menu {
    background: $black;
    width: 87%;
    top: 0;
    left: 0;
    height: 100%;
    position: relative;
    flex-direction: column;
    padding: 40px 20px 10px 20px;

    &__close {
        position: absolute;
        right: -39px;
        top: 10px;
        z-index: 1060; } }

