.cart-overlay {
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #B6C0CB;
  opacity: 0;
  z-index: 33;
  cursor: pointer;
  transition: 300ms;
  visibility: hidden;
}

.show-cart-overlay {
  visibility: visible;
  opacity: 0.8;
}

.overlay, .warning-overlay {
  z-index: 444;
}

/******** Header Top bar styles ******/
.top-bar {
  color: #B6C0CB;
  border-bottom: 1px solid #F4F7FB;
  z-index: 34;
}

@media only screen and (max-width: 767px) {
  .top-bar {
    height: 50px;
    display: flex;
    align-content: center;
  }
}

.top-bar__compare, .top-bar__favorite, .top-bar__email {
  color: #B6C0CB;
  transition: 300ms;
}

.top-bar__compare:hover, .top-bar__favorite:hover, .top-bar__email:hover {
  color: #B6C0CB;
}

.top-bar__compare:hover svg path, .top-bar__favorite:hover svg path, .top-bar__email:hover svg path {
  fill: #FE3362;
}

.top-bar__phone {
  color: #26323F;
}

.top-bar__phone:hover {
  color: #26323F;
}

.top-bar__phone-number-mobile {
  position: absolute;
  top: 15px;
  left: 50%;
