}

.top-bar__logo {
  width: 100px;
  max-width: 100%;
}

@media only screen and (max-width: 767px) {
  .top-bar__logo {
    display: flex;
    max-width: 70px;
    position: absolute;
    right: 0;
  }
}

.top-bar__search-btn:hover svg path {
  fill: #FE3362;
}

.top-bar__cart-amount {
  font-size: 16px;
  color: #26323F;
}

.top-bar__cart_total-amount {
  font-size: 16px;
  font-weight: bold;
}

.top-bar__search-box_hidden {
  z-index: 23;
  padding: 13px 15px;
  box-shadow: 0px 6px 20px -13px rgba(3, 3, 3, 0.1);
  display: flex;
  left: 120px;
  right: 112px;
  max-width: 80%;
  visibility: hidden;
  opacity: 0;
}

.top-bar__cart {
  display: none;
}

.top-bar__search-box_show {
  visibility: visible;
  opacity: 1;
}

.top-bar__hamburger-menu {
  margin-top: 15px;
  cursor: pointer;
}

.top-bar__hamburger-menu div {
  height: 4px;
  width: 38px;
  margin-bottom: 4px;
  background: #B6C0CB;
}

.top-bar__hamburger-menu .bar2 {
  width: 20px;
}

.top-bar__cart-button {
  z-index: 223;
  color: #B6C0CB;
  cursor: pointer;
}

.top-bar__circle-badge {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: #FE3362;
  border: none;
  top: -10px;
  right: -10px;
  font-size: 11px;
}

.top-bar__nav-link:hover svg path {
  fill: #FE3362;
}

.top-bar__mobile-filter {
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  position: absolute;
  background: white;
  text-align: center;
  font-size: 18px;
  padding: 10px 0;
}

.top-bar__close-filter {
  position: absolute;
  right: 0;
}

/******** Header Navigation bar styles ******/
.nav-bar {
  padding: 104px 0 30px;
  z-index: 33;
  position: fixed;
  transition: top 300ms;
}

@media only screen and (max-width: 991px) {
  .nav-bar {
    padding: 122px 0 20px;
  }
}

@media only screen and (max-width: 767px) {
  .nav-bar {
    padding: 0;
    visibility: hidden;
  }
}

.nav-bar__square {
  transition: 300ms;
  font-size: 6px;
  opacity: 0;
  margin-left: 5px;
}

.nav-bar__item {
  margin-right: 8px;
  color: #FE3362;
  transition: 300ms;
  position: relative;
}

@media only screen and (max-width: 991px) {
  .nav-bar__item {
    margin-right: 1px;
  }
}

.nav-bar__item:hover .nav-bar__square {
  opacity: 1;
}

.nav-bar__item .active-link, .nav-bar__item:hover .nav-bar__link {
  color: #FE3362;
}

.nav-bar__item .active-link svg, .nav-bar__item:hover .nav-bar__link svg {
  fill: #FE3362;
}

.nav-bar__item:hover .nav-bar__catalog-hover {
  transform: scaleY(1);
  opacity: 1;
}

.nav-bar__catalog-hover {
  display: block;
  opacity: 0;
  position: absolute;
  top: -5px;
  left: -25px;
  background: #ffffff;
  z-index: 20;
  transition: all .25s .1s ease-out;
  transform-origin: center top;
  transform: scaleY(0);
}

.nav-bar__link {
  color: #848E99;
  transition: 300ms;
  font-size: 16px;
}

@media only screen and (max-width: 1366px) {
  .nav-bar__link {
    padding: 0;
  }
}

@media only screen and (max-width: 991px) {
  .nav-bar__link {
    font-size: 14px;
  }
}

.nav-bar__catalog-hover-link {
  z-index: 33;
}

