/*****************  Filter Accordion *************/
.accordion-list {
  list-style: none;
  perspective: 900;
  padding: 0;
  margin: 0;
}

.accordion-list .accordion-item {
  position: relative;
  padding: 0;
  margin: 0;
  padding-bottom: 4px;
  padding-top: 18px;
  animation: flipdown 0.5s ease both;
}

.accordion-list .accordion-item__icon {
  position: absolute;
  transform: translate(-6px, 0);
  margin-top: 0;
  right: 0;
}

.accordion-list .accordion-item__icon:before, .accordion-list .accordion-item__icon:after {
  content: "";
  transition: all 0.25s ease-in-out;
  position: absolute;
  background-color: #848E99;
  width: 2px;
  height: 10px;
}

.accordion-list .accordion-item__icon:before {
  transform: translate(2px, 0) rotate(45deg);
}

.accordion-list .accordion-item__icon:after {
  transform: translate(-4px, 0px) rotate(-45deg);
}

.accordion-list .accordion-item__content {
  color: #26323F;
  line-height: 26px;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
  max-height: 800px;
  transition: all 0.25s ease-in-out;
  opacity: 1;
  transform: translate(0, 0);
  margin-top: 30px;
  z-index: 2;
}

.accordion-list .accordion-item input[type=checkbox] {
  position: absolute;
  cursor: pointer;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
}

.accordion-list .accordion-item input[type=checkbox]:checked ~ .accordion-item__content {
  margin-top: 0;
  max-height: 0;
  opacity: 0;
  transform: translate(0, 50%);
}

.accordion-list .accordion-item input[type=checkbox]:checked ~ i:before {
  transform: translate(-2px, 6px) rotate(45deg);
}

.accordion-list .accordion-item input[type=checkbox]:checked ~ i:after {
  transform: translate(-2px, 0) rotate(-45deg);
}

@keyframes flipdown {
  0% {
    opacity: 0;
    transform-origin: top center;
    transform: rotateX(-90deg);
  }
  5% {
    opacity: 1;
  }
  80% {
    transform: rotateX(8deg);
  }
  83% {
    transform: rotateX(6deg);
  }
  92% {
    transform: rotateX(-3deg);
  }
  100% {
    transform-origin: top center;
    transform: rotateX(0deg);
  }
}

