  display: flex;
  align-items: center;
  justify-content: center;
}

.stillage-button, .stillage-link-button {
  font-size: 14px;
  outline: none;
  text-decoration: none;
  width: 190px;
  height: 45px;
  background: none;
  border: 2px solid;
}

@media only screen and (max-width: 575px) {
  .stillage-button, .stillage-link-button {
    width: 100%;
  }
}

/***************** CUztomized Select box ***********/
.custom-select {
  appearance: none;
  position: relative;
}

.custom-select__icon {
  position: absolute;
  transform: translate(-5px, 0);
  transition: all .1s ease-in-out;
  top: 18px;
  right: 10px;
}

.custom-select__icon:before, .custom-select__icon:after {
  content: "";
  position: absolute;
  background-color: #26323F;
  width: 2px;
  height: 8px;
}

.custom-select__icon:before {
  transform: translate(-2px, 5px) rotate(45deg);
}

.custom-select__icon:after {
  transform: translate(-2px, 0) rotate(-45deg);
}

.select-arrow-active .custom-select__icon {
  top: 26px;
  transform: rotate(90deg);
}

.custom-select select {
  display: none;
}

.select-selected {
  color: #26323F !important;
  border: none !important;
  height: 50px;
  padding: 14px 0px !important;
  z-index: -4;
}

.select-items div, .select-selected {
  color: #ffffff;
  padding: 8px 16px;
  cursor: pointer;
  user-select: none;
}

.select-items {
  position: absolute;
  background-color: rgba(38, 50, 63, 0.96);
  box-shadow: 1px 0 0 #e6e6e6, -1px 0 0 #e6e6e6, 0 1px 0 #e6e6e6, 0 -1px 0 #e6e6e6, 0 3px 13px rgba(0, 0, 0, 0.08);
  top: 100%;
  left: 0;
  right: 0;
  z-index: 22;
  max-height: 200px;
  overflow-y: auto;
  text-align: center;
}

.select-hide {
  display: none;
}

.select-items div:hover, .same-as-selected {
  background-color: #26323F;
}

.same-as-selected {
  background-color: transparent;
  color: #FE3362 !important;
}

/************  Custom Radion Button *********************/
/* The radio container  of custom radion buttons*/
.radio-container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-weight: 600;
  user-select: none;
}

/* Hide the browser's default radio button */
.radio-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

/* Create a custom radio button */
.radio-checkmark {
  position: absolute;
  top: 0px;
  left: 0;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  border: 1px solid #848E99;
}

/* On mouse-over, add a grey background color */
.radio-container:hover input ~ .radio-checkmark {
  background-color: #d4dae0;
}

/* When the radio button is checked, add a blue background */
.radio-container input:checked ~ .radio-checkmark {
  background-color: transparent;
  border: 1px solid #FE3362;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.radio-checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.radio-container input:checked ~ .radio-checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.radio-container .radio-checkmark:after {
  top: 4px;
  left: 4px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #FE3362;
}
