@import '../../../sass/_vars';

.second-level-product-catalog {
    &__list {
        list-style: none;
        padding: 30px 0;
        min-width: 400px;
        height: 100%;
        border-left: 1px solid $gray;
        padding: 70px 0 40px;

        @include respond-to(tablet) {
            padding: 40px 0 30px; } }

    &__link {
        color: $dark-gray;
        padding-right: 3rem;
        padding-left: 3rem;
        transition: $transition;

        @include respond-to(tablet) {
            font-size: 14px; }

        &:hover {
            color: $primary; } } }
