.half-height {
    height: 550px;
    @include respond-to(phone-land) {
        height: 500px; }

    @include respond-to(phone) {
        height: 94vh; } }

.actions-slide1 {
    background-image: url('../img/actions/first-slide.png');
    @include respond-to(phone-land) {
        background-image: url('../img/actions/first-slide-mobile.png'); }

    &__title {
        width: 70%;
        @include respond-to(phone-land) {
            width: 100%; } }

    &__description {
        width: 70%;
        @include respond-to(phone-land) {
            width: 100%; } }

    &__rectangle {
        left: 0;
        max-width: 50%;
        height: 100%;
        @include respond-to(phone-land) {
            background-image: url('../img/actions/first-slide-mobile.png');
            transform: rotate(90deg);
            max-width: 100%;
            top: 0; } }

    &__exclamation-point {
        background: rgba(228, 228, 228, 0.20);
        width: 20px;
        height: 20px;
        border-radius: 50%; }

    &__cart {
        right: 0;
        max-width: 50%;
        @include respond-to(phone-land) {
            bottom: 0;
            max-width: 100%; } } }

.actions-slide2 {
    background-image: url('../img/actions/second-slide.png');
    @include respond-to(phone-land) {
        background-image: url('../img/actions/second-slide-mobile.png'); }

    &__text {
        margin-bottom: 200px; } }

.actions-slide3 {
    background-image: url('../img/actions/slide3.png');
    background-position: left;
    @include respond-to(phone-land) {
        background-image: url('../img/actions/slide3-mobile.png'); }

    &__text {
        width: 50%;
        @include respond-to(laptop) {
            width: 80%; } } }

.actions-slide4 {
    background-image: url('../img/actions/slide4.png');
    @include respond-to(phone-land) {
        background-image: url('../img/actions/slide4-mobile.png'); }

    &__text {
        width: 80%;
        @include respond-to(phone-land) {
            margin-bottom: 100px;
            width: 100%; } } }

.actions-slide5 {
    background-color: $gray;
    background-image: url('../img/actions/slide5.png');
    @include respond-to(phone-land) {
        background-image: url('../img/actions/slide5-mobile.png'); }
    &__text {
        font-size: 1.5rem; } }

.actions-slide6 {
    background-color: $black;
    background-image: url('../img/actions/slide6.png');
    background-position: left;
    @include respond-to(laptop) {
        background-position: bottom; }

    @include respond-to(phone-land) {
        background-image: url('../img/actions/slide6-mobile.png'); }
    &__text {
        font-size: 1.5rem; } }


/***************** Accordion Filters & Sorting  *************/

.catalog-sorting {
    margin-left: 225px; }

.actions-sidebar {
    min-width: 280px;
    margin-right: 30px;

    .filter {
        padding: 30px 20px; }

    .filter-content li:first-child i {
        cursor: pointer;
        font-size: 14px;
        border: 2px solid $light-gray;
        color: $primary; }

    .filter-content li:first-child {
        color: $primary; } }

.mobile-filter-wrapper {
  display: none;
  height: 100%;
  overflow: scroll;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 30;
  padding: 76px 30px;
  background-color: $white; }



/***************** Slider Price Range *************/
.noUi-horizontal .noUi-handle {
    width: 20px;
    height: 20px;
    border: none;
    right: -10px;
    left: 12px;
    border-radius: 50%;
    background: $primary;
    top: -8px;
    box-shadow: none;
    outline: none;
    cursor: pointer; }

.noUi-connect {
    height: 3px;
    width: 100%;
    background: $primary; }

.noUi-base, .noUi-connects {
    width: 100%;
    height: 3px;
    position: relative; }

.noUi-target {
    background: $gray;
    border: 0;
    box-shadow: none; }

.noUi-horizontal {
    height: 3px; }

.noUi-handle:after, .noUi-handle:before {
    background: none; }

.slider-wrapper {
  margin-top: 60px;
  padding: 0 10px; }

/*****************  Filter Accordion *************/

.accordion-list {
  list-style: none;
  perspective: 900;
  padding: 0;
  margin: 0;

  .accordion-item {
    position: relative;
    padding: 0;
    margin: 0;
    padding-bottom: 4px;
    padding-top: 18px;
    animation: flipdown 0.5s ease both;

    &__icon {
      position: absolute;
      transform: translate( -6px , 0 );
      margin-top: 0;
      right: 0;

      &:before , &:after {
        content: "";
        transition: all 0.25s ease-in-out;
        position: absolute;
        background-color: $dark-gray;
        width: 2px;
        height: 10px; }

      &:before {
        transform: translate( 2px, 0 ) rotate( 45deg ); }

      &:after {
        transform: translate(-4px, 0px) rotate( -45deg ); } }

    &__content {
      color: $black;
      line-height: 26px;
      letter-spacing: 1px;
      position: relative;
      overflow: hidden;
      max-height: 800px;
      transition: all 0.25s ease-in-out;
      opacity: 1;
      transform: translate( 0 , 0 );
      margin-top: 30px;
      z-index: 2; }

    input[type=checkbox] {
      position: absolute;
      cursor: pointer;
      width: 100%;
      height: 100%;
      z-index: 1;
      opacity: 0;

      &:checked {
        &~.accordion-item__content {
          margin-top: 0;
          max-height: 0;
          opacity: 0;
          transform: translate( 0 , 50% ); }

        &~i {
          &:before {
            transform: translate( -2px, 6px ) rotate( 45deg ); }

          &:after {
            transform: translate( -2px , 0 ) rotate( -45deg ); } } } } } }

@keyframes flipdown {
  0% {
    opacity: 0;
    transform-origin: top center;
    transform: rotateX(-90deg); }
  5% {
    opacity: 1; }
  80% {
    transform: rotateX(8deg); }
  83% {
    transform: rotateX(6deg); }
  92% {
    transform: rotateX(-3deg); }
  100% {
    transform-origin: top center;
    transform: rotateX(0deg); } }

/***************** End of price range *************/

.price-value {
    background: $light-gray;
    padding: 8px 10px; }


.price-range__value-low {
    top: 0;
    left: 0;
    color: $dark-gray; }

.price-range__value-up {
    top: 0;
    right: 0;
    color: $dark-gray; }

.product-info {
    p {
        padding-bottom: 10px; } }

.hidden {
 	display: none; }

.icon-button {
  	fill: $gray; }

.active {
	fill: $primary;
	color: $primary; }

.actions-pagination {
    &__turn-left,
    &__turn-right {
        height: 25px;
        width: 25px;
        background: $gray;
        color: $white; } }

/***************** Filter Price Checkboxes *************/
/* The container */
.checkbox-container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer; }

/* Hide the browser's default checkbox */
.checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0; }

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: $light-gray; }

/* Highlight container on hover */
.checkbox-container:hover {
    color: $primary; }

/* On mouse-over, add a grey background color */
.checkbox-container:hover input ~ .checkmark {
  background-color: $light-gray;
  height: 20px;
  width: 20px;
  border: 1px solid $dark-gray;
  transition: $transition; }

/* When the checkbox is checked, add a blue background */
.checkbox-container input:checked ~ .checkmark {
  background-color: $primary;
  height: 20px;
  width: 20px;
  border: 4px solid $light-gray;
  transition: $transition; }


/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none; }

/* Show the checkmark when checked */
.checkbox-container input:checked ~ .checkmark:after {
  display: block; }
