/*To activate warning/alert modal
 just add active class on needed events*/
.warning-overlay {
    @extend .overlay;

    &_active {
        @extend .overlay_active; } }

.warning-modal {
    @extend .modal;
    top: 50px;

    &_active {
        @extend .modal_active; } }

.warning {
    @extend .price-form;
    padding: 40px 20px;

    @include respond-to(phone) {
        top: 20%;
        width: 90%;
        height: auto;
        padding: 40px 20px; }

    &__close-btn {
        position: absolute;
        right: -6px;
        top: -33px;
        &:hover svg path {
            fill: $primary; } } }

