.working-way {
    &__background {
        font-size: 140px;
        font-weight: 600;
        opacity: 0.14; }

    &__steps {
        font-weight: 600; }

    &__row {
        @include respond-to(tablet) {
            overflow-x: scroll;
            height: 500px; } }

    &__height {
        height: 215px !important; }

    &__content {
        bottom: 50px;
        width: 200px;
        left: 0; } }
