.stillage-link-button {
    @extend .stillage-button;
    text-decoration: none !important;
    font-size: 16px;
    font-weight: 600;
    text-align: center;
    vertical-align: middle;
    display: flex;
    align-items: center;
    justify-content: center; }

.stillage-button {
    font-size: 14px;
    outline: none;
    text-decoration: none;
    width: 190px;
    height: 45px;
    background: none;
    border: 2px solid;
    @include respond-to(phone) {
        width: 100%; } }
