@import '../../sass/_vars';
@import '../../sass/_mixins/_media';

.site-header {
    height: 150px;
    @include respond-to(tablet) {
        height: 165px;
        padding-bottom: 0; }
    @include respond-to(phone-land) {
        height: 50px; } }

.cart-overlay {
    display: block;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $gray;
    opacity: 0;
    z-index: 33;
    cursor: pointer;
    transition: $transition;
    visibility: hidden; }

.show-cart-overlay {
    visibility: visible;
    opacity: 0.8; }

.overlay {
    z-index: 444; }

/******** Header Top bar styles ******/

.top-bar {
    color: $gray;
    border-bottom: 1px solid $light-gray;
    z-index: 34;

    @include respond-to(phone-land) {
        height: 50px;
        display: flex;
        align-content: center; }

    &__compare,
    &__favorite,
    &__email {
        color: $gray;
        transition: $transition;
        &:hover {
            color: $gray; }

        &:hover svg path {
            fill: $primary; } }

    &__phone {
        color: $black;

        &:hover {
            color: $black; } }

    &__phone-number-mobile {
        position: absolute;
        top: 15px;
        left: 50%;
        transform: translateX(-50%); }

    &__logo {
        width: 100px;
        max-width: 100%;

        @include respond-to(phone-land) {
            display: flex;
            max-width: 70px;
            position: absolute;
            right: 0; } }

    &__search-btn {
        &:hover svg path {
            fill: $primary; } }

    &__cart-amount {
        font-size: 16px;
        color: $black; }

    &__cart_total-amount {
        font-size: 16px;
        font-weight: bold; }

    &__search-box_hidden {
        z-index: 23;
        padding: 13px 15px;
        box-shadow: 0px 6px 20px -13px rgba(3,3,3,.1);
        display: flex;
        left: 120px;
        right: 112px;
        max-width: 80%;
        visibility: hidden;
        opacity: 0; }

    &__cart {
        display: none; }

    &__search-box_show {
        visibility: visible;
        opacity: 1; }


    &__hamburger-menu {
        margin-top: 15px;
        cursor: pointer;

        div {
            height: 4px;
            width: 38px;
            margin-bottom: 4px;
            background: $gray; }

        .bar2 {
            width: 20px; } }

    &__cart-button {
        z-index: 223;
        color: $gray;
        cursor: pointer; }

    &__circle-badge {
        height: 20px;
        width: 20px;
        border-radius: 50%;
        background: $primary;
        border: none;
        top: -10px;
        right: -10px;
        font-size: 11px; }

    &__nav-link {
        &:hover svg path {
            fill: $primary; } }

    &__mobile-filter {
        display: none;
        width: 100%;
        height: 100%;
        top: 0;
        right: 0;
        position: absolute;
        background: white;
        text-align: center;
        font-size: 18px;
        padding: 10px 0; }

    &__close-filter {
        position: absolute;
        right: 0; } }


/******** Header Navigation bar styles ******/
.nav-bar {
    padding: 104px 0 30px;
    z-index: 33;
    position: fixed;
    transition: top $transition;

    @include respond-to(tablet) {
        padding: 122px 0 20px; }

    @include respond-to(phone-land) {
        padding: 0;
        visibility: hidden; }

    &__square {
        transition: $transition;
        font-size: 6px;
        opacity: 0;
        margin-left: 5px; }


    &__item {
        margin-right: 8px;
        color: $primary;
        transition: $transition;
        position: relative;

        @include respond-to(tablet) {
            margin-right: 1px; }

        &:hover .nav-bar__square {
            opacity: 1; }

        .active-link, &:hover .nav-bar__link {
            color: $primary;
            svg {
                fill: $primary; } }

        &:hover .nav-bar__catalog-hover {
            transform: scaleY(1);
            opacity: 1; } }

    &__catalog-hover {
        display: block;
        opacity: 0;
        position: absolute;
        top: -5px;
        left: -25px;
        background: $white;
        z-index: 20;
        transition: all .25s .1s ease-out;
        transform-origin: center top;
        transform: scaleY(0); }


    &__link {
        color: $dark-gray;
        transition: $transition;
        font-size: 16px;

        @include respond-to(laptop) {
            padding: 0; }

        @include respond-to(tablet) {
            font-size: 14px; } }

    &__catalog-hover-link {
        z-index: 33; } }
